(function() {
  'use strict';

  angular
    .module('whereToGo', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr', 'LocalStorageModule', 'datatables', 'uiGmapgoogle-maps', 'datatables']);

})();

(function() {
	'use strict';

	angular
		.module('whereToGo')
		.controller('MainController', MainController);

	/** @ngInject */
	function MainController($scope, $modal, localStorageService, DTOptionsBuilder, DTColumnDefBuilder) {
		var vm = this;

		/* datatables */

		vm.dtOptions = DTOptionsBuilder.newOptions();
		vm.DTColumnDefs = [
			DTColumnDefBuilder.newColumnDef(2).withOption("sType", "num"),
			DTColumnDefBuilder.newColumnDef(3).withOption("sType", "num")
		];

		/* private */

		vm.geocode = function(name, callback) {
			if(name == null || name == "") {
				callback({});
			}

			var geocoder = new google.maps.Geocoder();
			geocoder.geocode({'address': name}, function(results, status) {
				if(status == 'OK') {
					var latitude = results[0].geometry.location.lat();
					var longitude = results[0].geometry.location.lng();
					callback({ "latitude": latitude, "longitude": longitude });
				}
			});
		};

		/* home */

		vm.home = localStorageService.get('home');
		vm.home = vm.home || {};
		vm.homeForm = vm.home;

		$scope.$watch(function(){
			return vm.home;
		}, function () {
			localStorageService.set('home', vm.home);
		}, true);

		vm.showHomeForm = function() {
			vm.homeModalInstance = $modal.open({
				templateUrl: "homeForm",
				scope: $scope
			});
		};

		vm.geocodeHome = function() {
			vm.geocode(vm.homeForm.name, function(result) {
				vm.homeForm.latitude = result.latitude;
				vm.homeForm.longitude = result.longitude;
				$scope.$apply();
			});
		};

		vm.registerHome = function() {
			vm.home.name = vm.homeForm.name;
			vm.home.latitude = vm.homeForm.latitude;
			vm.home.longitude = vm.homeForm.longitude;
			vm.homeModalInstance.close();
		};

		/* place */

		vm.placeMap = localStorageService.get('placeMap');
		vm.placeMap = vm.placeMap || {};

		$scope.$watch(function(){
			return vm.placeMap;
		}, function () {
			localStorageService.set('placeMap', vm.placeMap);
		}, true);

		vm.deletePlace = function(id) {
			delete vm.placeMap[id];
			vm.placeModalInstance.close();
		};

		vm.recalculate = function() {
			var origin = new google.maps.LatLng(vm.home.latitude, vm.home.longitude);
			var targetList = [];
			angular.forEach(vm.placeMap, function(place){
				targetList.push(new google.maps.LatLng(place.latitude, place.longitude));
			});

			var service = new google.maps.DistanceMatrixService();
			service.getDistanceMatrix({
				origins: [origin],
				destinations: targetList,
				travelMode: google.maps.TravelMode.DRIVING,
				avoidHighways: false,
				avoidTolls: false
			}, function(response, status) {
				if (status == google.maps.DistanceMatrixStatus.OK) {
					var i=0;
					angular.forEach(vm.placeMap, function(place){
						var element = response.rows[0].elements[i];
						place.distance = element.distance.value;
						place.duration = element.duration.value;
						i++;
					});
					$scope.$apply();
				}
			});
		};

		/* map */

		vm.map = { center: { latitude: vm.home.latitude, longitude: vm.home.longitude }, zoom: 10 };

		/* placeForm */

		vm.showPlaceForm = function(id) {
			if(id == null) {
				vm.placeForm = angular.copy(vm.home);
			} else {
				vm.placeForm = angular.copy(vm.placeMap[id]);
			}

			vm.placeModalInstance = $modal.open({
				templateUrl: "placeForm",
				scope: $scope
			});

			vm.placeModalInstance.rendered.then(function(){
				if(vm.placeFormMap.control.getGMap !== void 0) {
					var gmap = vm.placeFormMap.control.getGMap();
					google.maps.event.trigger(gmap, "resize");
				}

				vm.placeFormMap.center = { latitude: vm.placeForm.latitude, longitude: vm.placeForm.longitude };
				vm.placeFormMap.marker = vm.placeForm;
			});
		};

		vm.geocodePlace = function() {
			vm.geocode(vm.placeForm.name, function(result) {
				vm.placeForm.latitude = result.latitude;
				vm.placeForm.longitude = result.longitude;
				vm.placeFormMap.center = { latitude: vm.placeForm.latitude, longitude: vm.placeForm.longitude };
				$scope.$apply();
			});
		};

		vm.createOrUpdatePlace = function() {
			var place = angular.copy(vm.placeForm);
			if(place.id == null) {
				place.id = UUIDjs.create().toString();
			}
			vm.placeMap[place.id] = place;
			vm.placeForm = {};
			vm.placeModalInstance.close();
		};

		/* placeFormMap */

		vm.placeFormMap = {};
		//vm.placeFormMap.center = { latitude: vm.home.latitude, longitude: vm.home.longitude };
		//vm.placeFormMap.marker = vm.placeForm;
		vm.placeFormMap.style = { "display": "block" };
		vm.placeFormMap.control = {};

		vm.placeFormMap.toggle = function() {
			if(vm.placeFormMap.style.display == "none") {
				vm.placeFormMap.style = { "display": "block"};
			} else {
				vm.placeFormMap.style = { "display": "none"};
			}
		};

		vm.placeFormMap.setMarker = function(maps, eventName, args) {
			vm.placeForm.latitude = args[0].latLng.lat();
			vm.placeForm.longitude = args[0].latLng.lng();
			$scope.$apply();
		};
	}
})();

(function() {
  'use strict';

  angular
    .module('whereToGo')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('whereToGo')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('whereToGo')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('whereToGo')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("whereToGo").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><h1>Where To Go</h1><div class=\"row\" style=\"margin:20px;\"><span ng-if=\"main.home.latitude != null && main.home.longitude != null\" class=\"label label-default\" style=\"margin-right:12px\">Home is regsterd</span> <input ng-if=\"main.home.latitude != null && main.home.longitude != null\" type=\"button\" value=\"Update\" class=\"btn btn-primary\" style=\"margin-right:12px\" ng-click=\"main.showHomeForm()\"> <input ng-if=\"main.home.latitude != null && main.home.longitude != null\" type=\"button\" value=\"Recalculate\" class=\"btn btn-primary\" ng-click=\"main.recalculate()\"> <span ng-if=\"main.home.latitude == null || main.home.longitude == null\" class=\"label label-danger\" style=\"margin-right:12px\">Home is not regsterd</span> <input ng-if=\"main.home.latitude == null || main.home.longitude == null\" type=\"button\" value=\"Register\" class=\"btn btn-primary\" ng-click=\"main.showHomeForm()\"> <input type=\"button\" value=\"New Place\" class=\"btn btn-primary pull-right\" ng-click=\"main.showPlaceForm()\"></div><table datatable=\"ng\" dt-options=\"main.dtOptions\" dt-column-defs=\"main.DTColumnDefs\" class=\"table table-striped\"><thead><tr><th>Category</th><th>Name</th><th>Distance (km)</th><th>Duration (min)</th><th>Memo</th></tr></thead><tr ng-repeat=\"(id, place) in main.placeMap\" ng-click=\"main.showPlaceForm(place.id)\"><td>{{ place.category }}</td><td><a target=\"_blank\" href=\"{{ place.url }}\">{{ place.name }}</a></td><td>{{ (place.distance / 1000).toFixed(0) }}</td><td>{{ (place.duration / 60 ).toFixed(0) }}</td><td>{{ place.memo }}</td></tr></table><div style=\"margin-top:10px;\"><ui-gmap-google-map center=\"main.map.center\" zoom=\"main.map.zoom\" idkey=\"0\"><ui-gmap-marker ng-repeat=\"(id, place) in main.placeMap\" coords=\"{latitude:place.latitude, longitude:place.longitude}\" options=\"{text:place.name}\" events=\"marker.events\" idkey=\"place.id\"><ui-gmap-window show=\"true\">{{ place.name }}</ui-gmap-window></ui-gmap-marker></ui-gmap-google-map></div><script type=\"text/ng-template\" id=\"homeForm\"><div class=\"modal-body\"> <div style=\"margin: 20px;\"> <form class=\"form-horizontal\" ng-submit=\"main.registerHome()\"> <div class=\"form-group form-inline\"> <input type=\"text\" placeholder=\"name\" class=\"form-control\" style=\"width:250px;margin-right:12px;\" ng-model=\"main.homeForm.name\"> <input type=\"button\" value=\"Geocode\" class=\"btn\" ng-click=\"main.geocodeHome()\"> </div> <div class=\"form-group form-inline\"> <input type=\"text\" placeholder=\"latitude\" class=\"form-control\" style=\"width:120px;margin-right:12px;\" ng-model=\"main.homeForm.latitude\"> <input type=\"text\" placeholder=\"longitude\" class=\"form-control\" style=\"width:120px;margin-right:12px;\" ng-model=\"main.homeForm.longitude\"> </div> <div class=\"form-group\"> <input type=\"submit\" value=\"Register\" class=\"btn btn-primary\"> </div> </form> </div> </div></script><script type=\"text/ng-template\" id=\"placeForm\"><div class=\"modal-body\"> <div style=\"margin: 20px;\"> <form class=\"form-horizontal\" ng-submit=\"main.createOrUpdatePlace()\"> <div class=\"form-group form-inline\"> <input type=\"text\" placeholder=\"category\" class=\"form-control\" style=\"width:120px;margin-right:12px;\" ng-model=\"main.placeForm.category\"> <input type=\"text\" placeholder=\"name\" class=\"form-control\" style=\"width:200px;margin-right:12px;\" ng-model=\"main.placeForm.name\"> <input type=\"button\" value=\"Geocode\" class=\"btn\" ng-click=\"main.geocodePlace()\"> </div> <div class=\"form-group form-inline\"> <input type=\"text\" placeholder=\"latitude\" class=\"form-control\" style=\"width:120px;margin-right:12px;\" ng-model=\"main.placeForm.latitude\"> <input type=\"text\" placeholder=\"longitude\" class=\"form-control\" style=\"width:120px;margin-right:12px;\" ng-model=\"main.placeForm.longitude\"> <input type=\"button\" value=\"Toggle Map\" class=\"btn\" ng-click=\"main.placeFormMap.toggle()\"> </div> <div id=\"mapChooser\" class=\"form-group\" ng-style=\"main.placeFormMap.style\" > <ui-gmap-google-map pan=\"true\" center=\"main.placeFormMap.center\" zoom=\"12\" idKey=\"100\" events=\"{dblclick:main.placeFormMap.setMarker}\" control=\"main.placeFormMap.control\" options=\"{disableDoubleClickZoom:true}\"> <ui-gmap-marker coords=\"main.placeFormMap.marker\" idkey=\"2\"></ui-gmap-marker> </ui-gmap-google-map> </div> <div class=\"form-group\"> <input type=\"text\" placeholder=\"URL\" class=\"form-control\" style=\"width:450px;\" ng-model=\"main.placeForm.url\"> </div> <div class=\"form-group\"> <textarea placeholder=\"memo\" class=\"form-control\" style=\"width:450px\" ng-model=\"main.placeForm.memo\"></textarea> </div> <div class=\"form-group\"> <input type=\"hidden\" value=\"{{ main.placeForm.id }}\"> <input ng-if=\"main.placeForm.id == null\" type=\"submit\" value=\"Add\" class=\"btn btn-primary\"> <input ng-if=\"main.placeForm.id != null\" type=\"submit\" value=\"Update\" class=\"btn btn-primary\" style=\"margin-right:12px;\"> <input ng-if=\"main.placeForm.id != null\" ng-click=\"main.deletePlace(main.placeForm.id)\" type=\"button\" value=\"Delete\" class=\"btn btn-danger\"> </div> </form> </div> </div></script></div>");}]);